@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah&display=swap);
.spinner{position:absolute;top:calc(50% - 50px);left:calc(50% - 50px);width:100px;height:100px;-webkit-animation:spinner 0.75s linear infinite;animation:spinner 0.75s linear infinite}.spinner span{position:absolute;width:100%;height:100%;border-radius:50%}.spinner .spinner-inner-1{background:-webkit-gradient(linear, left top, right top, from(rgba(0,140,158,0)), color-stop(50%, rgba(0,140,158,0)), color-stop(51%, #008c9e));background:linear-gradient(to right, rgba(0,140,158,0) 0%, rgba(0,140,158,0) 50%, #008c9e 51%)}.spinner .spinner-inner-2{background:-webkit-gradient(linear, left bottom, left top, from(rgba(7,36,41,0)), to(#072429));background:linear-gradient(to top, rgba(7,36,41,0) 0%, #072429 100%)}.spinner .spinner-inner-3{top:10px;left:10px;width:80px;height:80px;background:#072429}@-webkit-keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{border-radius:10px}::-webkit-scrollbar-thumb{background:#0e4a55;border-radius:10px}::-webkit-scrollbar-thumb:hover{background:#167180}#pagination{max-width:1050px;margin:0 auto;position:relative;font-size:15px}#pagination ul{list-style-type:none}#pagination a{position:relative;float:left;color:white;margin:0 4px;padding:6px 9px 5px 9px;text-decoration:none;color:#ddd}#pagination a:hover{color:#7ad8e8}#pagination a:active{color:#7ad8e8}#grid{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;max-width:1300px;margin:auto}#grid ul{list-style-type:none}#grid .flip-card-container{max-width:280px;margin:auto;height:500px;border-radius:10px}#grid .flip-card{position:relative;width:100%;height:100%;-webkit-transition:-webkit-transform 0.6s;transition:-webkit-transform 0.6s;transition:transform 0.6s;transition:transform 0.6s, -webkit-transform 0.6s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}#grid:checked ~ .flip-card{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}#grid .flip-card-container:hover .flip-card{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}#grid .flip-card-front,#grid .flip-card-back{border-radius:10px;position:relative;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}#grid .flip-card-front{width:100%;position:relative;background:rgba(255,255,255,0.438);box-shadow:0 6px 10px -5px rgba(0,0,0,0.2);-webkit-transition:all 0.3s ease;transition:all 0.3s ease;text-align:left;-webkit-perspective:1000px;perspective:1000px}#grid .flip-card-front img{width:100%;border-radius:10px 10px 0px 0px;-webkit-filter:inset 5px 5px 5px #222222;filter:inset 5px 5px 5px #222222}#grid .flip-card-front:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px);box-shadow:0 20px 60px -25px rgba(255,255,255,0.5)}#grid .flip-card-back{position:absolute;top:0;background:rgba(255,255,255,0.438);-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}#grid .shadow{display:block;position:relative}#grid .shadow:before{bottom:4px;box-shadow:inset 0px -50px 20px -10px rgba(0,0,0,0.6);border-radius:10px 10px 0px 0px;content:"";height:50%;position:absolute;width:100%}#grid h2{width:100%;position:absolute;top:225px;text-align:right;right:10px;font-size:15px;font-weight:bold;text-transform:uppercase;color:white}#grid h3{width:100%;position:absolute;top:250px;text-align:right;right:10px;font-size:10px;font-weight:bold;text-transform:uppercase;color:white}.info{font-weight:bold;font-size:9px;text-transform:uppercase;margin:10px 20px -15px 20px}.info span{text-align:left;width:40%;display:inline-block;color:rgba(255,255,255,0.719);line-height:30px}.info p{text-align:right;width:59%;display:inline-block;color:white}.info hr{display:block;height:1px;border:0;border-top:1px solid rgba(255,255,255,0.541);margin:0;padding:0}.card-back-info{font-weight:bold;text-transform:uppercase;margin:10px 20px -10px 20px}.card-back-info h1{color:white;text-align:center}.card-back-info span{display:inline-block;margin:2px 2px;height:20px;width:20px;line-height:20px;border-radius:50%;font-size:10px;background-color:rgba(0,0,0,0.377);color:white;text-align:center}body{background-color:#072429;font-family:"Oswald", sans-serif}.title{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:auto;color:#64d2e5;text-transform:uppercase;font-family:'Gloria Hallelujah', cursive}.c-count{margin-top:-10px;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;text-align:right;color:#1d97ac;text-transform:uppercase;font-size:15px}

